import 'lazysizes/plugins/bgset/ls.bgset.js';
import 'lazysizes/plugins/parent-fit/ls.parent-fit.js';
// slider
import Swiper, {Pagination, Parallax, Mousewheel, FreeMode, Autoplay} from "swiper";
// modal
import {getAppHeight} from "./_func/_jq/getAppHeight";
import {simpleModal} from "./_func/_jq/modalMagnific";
/* ------------------- */

document.addEventListener('DOMContentLoaded', () => {
    getAppHeight();
    var touchstartY = 0;
    var touchendY = 0;
    /* main slider */
    var mainSlider = new Swiper("#main-slider", {
        modules: [Parallax, Mousewheel],
        speed: 1800,
        parallax: true,
        direction: "vertical",
        watchSlidesProgress: true,
        mousewheelControl: true,
        mousewheel: true,
        allowTouchMove: false,
        on: {
            slideChange: function (e){
                if(e.activeIndex == 0){
                    $('#kid').attr('data-position', 0);
                }
                if(e.activeIndex == 1){
                    $('#kid').attr('data-position', 1);
                }
                if(e.activeIndex == 2){
                    $('#kid').attr('data-position', 2);
                    setTimeout(function (){
                        qualitySlider.autoplay.start();
                    }, 3500);

                }
            }
        }
    });
    /* swipe detection */
    $(window).on('touchstart', function (event){
        touchstartY = event.changedTouches[0].screenY;
    });
    $(window).on('touchend', function (event){
        touchendY = event.changedTouches[0].screenY;
        handleGesure();
    });
    function handleGesure() {
        if(!$('body').hasClass('overflow')){
            if (touchendY < touchstartY && touchstartY - touchendY > 50) {
                mainSlider.slideNext();
            }
            if (touchendY > touchstartY && touchendY - touchstartY > 50) {
                mainSlider.slidePrev();
            }
        }
    }

    /* main page sliders */
    /* TODO bag with slider */
    var qualitySlider = new Swiper("#quality-slider", {
        modules: [ Pagination, Autoplay, FreeMode ],
        speed: 3500,
        slidesPerView: 'auto',
        centeredSlides: true,
        loopedSlides: 2,
        spaceBetween: 0,
        loop: true,
        allowTouchMove: false,
        autoplay: {
            delay: 0,
            disableOnInteraction: false
        },
        freeMode: true,
        pagination: {
            el: ".swiper-pagination",
            clickable: true,
        },
        on: {
            init: function () {
                this.autoplay.stop();
            }
        }
    });
    document.querySelectorAll('.main-popular .popular-slider').forEach(popularSlider => {
        var popularSlider = new Swiper(popularSlider, {
            speed: 800,
            loop: false,
            slidesPerView: "auto",
            spaceBetween: 0
        });
    });
    // change popular sliders
    $('.main-popular .popular-list .popular-list__link').click(function (){
        var curType = $(this).attr('data-type');
        $('.main-popular .popular-list .popular-list__link').removeClass('active');
        $('.main-popular .main-popular__content .popular-slider').hide();
        $(this).addClass('active');
        $('.main-popular .main-popular__content .popular-slider[data-type="'+ curType +'"]').fadeIn(600);
    });
    var fathersSlider = new Swiper("#fathers-slider", {
        modules: [ Pagination ],
        speed: 800,
        spaceBetween: 0,
        slidesPerView: 'auto',
        pagination: {
            el: ".swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            421: {
                spaceBetween: 0,
                centeredSlides: true
            },
            768: {
                spaceBetween: 0,
                centeredSlides: false
            },
            1024: {
                spaceBetween: 0,
                centeredSlides: false
            }
        }

    });
    var lifehacksSlider = new Swiper('#lifehacks-slider', {
        modules: [ Pagination ],
        speed: 800,
        loop: false,
        slidesPerView: "auto",
        spaceBetween: 0,
        pagination: {
            el: ".swiper-pagination",
            clickable: true,
        }
    });
    /* phone mask */
    $("input[name='phone']").mask("+7 (999) 999-9999");
    /* file change */
    $('input[type="file"]').change(function (){
        $(this).parent().find('.label span').html(' ');
    })
    /* quiz links */
    $('#quiz_modal input[name="age"]').change(function (){
        $('#quiz_modal .modal-content__step[data-step="5"] .next-link').attr('data-step', $('#quiz_modal input[name="age"]:checked').attr('data-step'));
    });
    $('.js-step-btn').click(function (){
        var curStep = $(this).attr('data-step');
        $('#quiz_modal .modal-content__step').hide();
        $('#quiz_modal .modal-content__step[data-step="'+ curStep +'"]').fadeIn(500);
    })
    /* modal */
    simpleModal();

    // tmp to check messages
    /*
    setTimeout(function (){
        $('#history_modal .modal-form-wrapper').hide();
        $('#history_modal .modal-message.success-message').fadeIn();
    }, 4000)
     */
})